export default "import React from 'react';\nimport { FlowsheetResultCell } from 'terra-clinical-result/lib/index';\nimport classNames from 'classnames/bind';\nimport styles from '../Examples.module.scss';\n\nconst cx = classNames.bind(styles);\n\nconst multipleResultBPValues = [\n  {\n    id: '1602328271-1',\n    systolic: {\n      eventId: '1602328271-1.1',\n      result: {\n        value: '111',\n        unit: 'mmHg',\n      },\n      interpretation: 'high',\n      isModified: true,\n      hasComment: true,\n    },\n    diastolic: {\n      eventId: '1602328271-1.2',\n      result: {\n        value: '77',\n        unit: 'mmHg',\n      },\n      interpretation: 'low',\n      isModified: true,\n      hasComment: true,\n    },\n  },\n  {\n    id: '1602328271-2',\n    systolic: {\n      eventId: '1602328271-2.1',\n      result: {\n        value: '111',\n        unit: 'mmHg',\n      },\n      interpretation: 'high',\n      isModified: true,\n      hasComment: true,\n    },\n    diastolic: {\n      eventId: '1602328271-2.2',\n      result: {\n        value: '77',\n        unit: 'mmHg',\n      },\n      interpretation: 'low',\n      isModified: true,\n      hasComment: true,\n    },\n  },\n  {\n    id: '1602328271-3',\n    systolic: {\n      eventId: '1602328271-3.1',\n      result: {\n        value: '111',\n        unit: 'mmHg',\n      },\n      isModified: true,\n      hasComment: true,\n    },\n    diastolic: {\n      eventId: '1602328271-3.2',\n      result: {\n        value: '77',\n        unit: 'mmHg',\n      },\n      isModified: true,\n      hasComment: true,\n    },\n  },\n];\n\nexport default () => (\n  <React.Fragment>\n    <div className={cx('mock-flowsheet-resultcolumn')}>\n      <div className={cx('mock-flowsheet-resultcolumn-cell')}>\n        <FlowsheetResultCell resultDataSet={multipleResultBPValues} hideUnit />\n      </div>\n      <div className={cx(['mock-flowsheet-resultcolumn-cell', 'empty'])} />\n    </div>\n    <div className={cx('mock-flowsheet-resultcolumn')}>\n      <div className={cx(['mock-flowsheet-resultcolumn-cell', 'empty'])} />\n      <div className={cx(['mock-flowsheet-resultcolumn-cell', 'empty'])} />\n    </div>\n  </React.Fragment>\n);\n"